.HomePage {
  position: relative;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  z-index: 3; }

.intro-text {
  color: #fff;
  text-align: center;
  font-size: 2.5rem; }
  .intro-text span {
    color: #037fff;
    font-size: 3.5rem; }

.intro-text-para {
  text-align: center; }

.icons {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1.5rem 0; }
  .icons .icon-holder {
    margin-right: 1rem; }
    .icons .icon-holder .icon {
      font-size: 2.8rem;
      color: #a4acc4;
      transition: all .4s ease-in-out; }
    .icons .icon-holder .fb:hover {
      color: blue; }
    .icons .icon-holder .gh:hover {
      color: #eaee08; }
    .icons .icon-holder .ig:hover {
      color: #f003f0; }

.ContactItem {
  width: 100%; }
  .ContactItem:not(:last-child) {
    margin-bottom: 1rem; }
  .ContactItem .contact {
    display: flex;
    align-items: center;
    background-color: #191d2b;
    padding: 1.5rem 2rem; }
    .ContactItem .contact .right-items {
      margin-left: 1rem;
      width: 80%; }
    .ContactItem .contact img {
