      border-radius: 25px;
      width: 16%; }
    .ContactItem .contact .right-items h6 {
      font-size: 1.2rem;
      font-weight: 500;
      color: #037fff; }

.ContactPage {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 2rem; }
  @media screen and (max-width: 1270px) {
    .ContactPage {
      grid-template-columns: repeat(1, 1fr); }
      .ContactPage .map-sect {
        width: 100%;
        height: 50vh;
        margin-bottom: 1rem; } }
  .ContactPage .contact-sect {
    width: 100%;
    display: flex;
    justify-content: space-around;
    flex-direction: column; }

.title {
  margin-bottom: 5rem; }

.App .side-bar {
  width: 20%;
  height: 100vh;
  background-color: #191d28;
  position: fixed;
  border-right: 1px solid #2e344e; }

.App .main-content {
  width: 80%;
  margin-left: 20%;
  background-color: #10121b;
  min-height: 100vh;
  position: relative;
  display: grid; }
  .App .main-content .content {
    margin: 5rem 10rem;
    z-index: 2; }

.NavBar {
  height: 100vh; }
  .NavBar .nav {
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: column; }
    .NavBar .nav .my-image {
      width: 100%;
      text-align: center;
      margin-top: 1rem;
      border-bottom: 1px solid #2e344e; }
      .NavBar .nav .my-image img {
        width: 70%;
        border-radius: 100%;
        border: 5px solid #2e344e;
        margin-bottom: 1rem; }
    .NavBar .nav .nav-items {
      width: 100%; }
      .NavBar .nav .nav-items .nav-item {
        text-align: center;
        list-style: none; }
        .NavBar .nav .nav-items .nav-item a {
          text-decoration: none;
          color: inherit;
          font-size: inherit;
          display: block;
          padding: 0.5rem 0;
          position: relative; }
          .NavBar .nav .nav-items .nav-item a::before {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            width: .4rem;
            height: 100%;
            background-color: #0381ff33;
            transform-origin: bottom;
            transform: scale(0);
            overflow-x: hidden;
            transition: transform 0.4s, 0.2s width 0.3s cubic-bezier(0.75, 0.18, 0.41, 0.97); }
          .NavBar .nav .nav-items .nav-item a:hover::before {
            width: 100%;
            transform: scale(1); }
    .NavBar .nav .footer {
      width: 100%;
      border-top: 1px solid #2e344e; }
      .NavBar .nav .footer p {
        text-align: center;
        padding: 1rem 0; }

.active {
  background-color: #037fff;
  color: black !important; }

.title {
  text-transform: uppercase;
  font-size: 2rem;
  color: #fff;
  position: relative;
  padding-bottom: 1rem;
  letter-spacing: 3px; }
  .title::before {
    content: " ";
    bottom: 0;
    position: absolute;
    left: 0;
    background-color: #0381ff38;
    width: 6rem;
    height: .5rem;
    border-radius: 50px; }
  .title::after {
    content: " ";
    bottom: 0;
    position: absolute;
    left: 0;
    background-color: #037fff;
    width: 4rem;
    height: .5rem;
    border-radius: 50px; }
  .title span {
    position: absolute;
    top: 15%;
    left: 0;
    font-size: 4rem;
    opacity: 0.07; }

.AboutPage {
  z-index: 3;
  line-height: 2; }

.ImageSection {
  display: flex;
  margin-top: 2rem;
  margin-bottom: 3rem; }
  .ImageSection .img {
    width: 100%;
    height: 60vh; }
    .ImageSection .img img {
      width: 100%;
      object-fit: cover;
      height: 80%;
      border-radius: 10px; }
  .ImageSection .about-info {
    margin-top: -1.5rem;
    margin-left: 2rem; }
    .ImageSection .about-info h4 {
      font-size: 2.5rem; }

.about-details {
  display: flex;
  margin-top: 1rem; }
  .about-details .right-section {
    margin-left: 1rem; }

.btn {
  padding: 1rem 2rem;
  background-color: #037fff;
  outline: none;
  border: none;
  font-family: inherit;
  font-size: inherit;
  color: white;
  text-transform: uppercase;
  cursor: pointer;
  letter-spacing: 2px;
  margin-top: 1rem;
  position: relative;
  border-radius: 50px;
  overflow-x: hidden; }
  .btn::after {
    position: absolute;
    content: "";
    width: 0;
    height: 0.5rem;
    left: 0;
    bottom: 0;
    background-color: #161158;
    transition: all 0.4s ease-in-out; }
  .btn:hover::after {
    width: 100%; }

.skillsContainer {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 4rem;
  row-gap: 3rem;
  margin: 4rem 0; }
  @media screen and (max-width: 1100px) {
