  align-items: center; }
  .skillsContainer .skill-bar .skill-progress {
    width: 100%; }
    .skillsContainer .skill-bar .skill-progress .progress {
      width: 100%;
      height: 0.5rem;
      border-radius: 50px;
      margin-left: 1.1rem;
      background-color: #0381ff3f;
