  .ServicesSection .service .service-content {
    padding: 1rem; }
    .ServicesSection .service .service-content .s-title {
      font-size: 1.6rem;
      font-weight: 500;
      position: relative;
      padding-bottom: 0.5rem;
      margin: 1rem 0; }
      .ServicesSection .service .service-content .s-title::after {
