.services-container {
  display: flex;
  justify-content: space-between;
  margin-top: 4rem; }
  @media screen and (max-width: 1400px) {
    .services-container {
      flex-direction: column;
      margin-bottom: 2rem; } }

.BlogsPage {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 2.5rem; }
  @media screen and (max-width: 1080px) {
