  border-radius: 50px; }

body::-webkit-scrollbar-track {
  background-color: #037fff; }

.tsparticles-canvas-el {
  position: absolute;
  height: 100%;
  width: 100%;
  z-index: 1; }

span {
  color: #037fff; }
